
  
  .main {
    background-color:wheat;
    height: 30em;
    width: 30%;
    margin: 5em auto;
  }

  @media (max-width:900px) {
    .main {
      width:50%;
      margin: 4em auto;
    }
  }

  @media (max-width:600px) {
    .main {
      width: 80%;
      height: 25em;
      margin: 6em auto;
    }
  }

  @media (max-width:400px) {
    .main {
      height: 25em;
      margin: 6em auto;
    }
  }
  
  .todoform {
    display: flex;
    justify-content: center;
    vertical-align: middle;
  }
  
  .todoform input {
    background-color:rgba(128, 128, 128, 0.2);
    border:0;
    height: 4em;
    width: 50%;
    padding:0 2em;
    margin: 2em;
    text-align: center;
    color:black;
    border-radius: 2em;
  }

  @media (max-width:600px) {
    .todoform input {
      width: 80%;
      margin: 2em 1em;
    }
  }
  
  .todoform input::placeholder {
    color: rgb(150, 145, 145);
  }
  
  .todoform input:focus,.todoform button:focus {
    outline: none;
  }
  
  .todoform button {
      height: 4em;
      background-color: deepskyblue;
      border: 1px black solid;
      padding: 1em;
      font-weight: bolder;
      margin: 2em;
      border-radius:2em;
      cursor: pointer;
  }

  @media (max-width:600px) {
    .todoform button {
      margin: 2em 1em;
    }
  }