.mylist {
    background-color: goldenrod;
    border-radius: 1em;
    width: 90%;
    height: 3em;
    margin: 1em auto;
}

.mylist p {
    padding:.8em;
    position: relative;
}

.mylist p span {
    position: absolute;
    right: 1em;
    color: rgba(139, 0, 0, 0.7);
    cursor: pointer;
}

.faicons {
    font-size: 1em;
}

.mylist p input {
    border: 0;
    background-color:transparent;
    width:80%;
    color: whitesmoke;
    font-size: 1em;
}

@media (max-width:600px) {
    .mylist p input {
        width: 70%;
    }
}

.mylist p input:focus {
    outline: none;
    border: none;
}

.obut {
    background:transparent;
    border:none ;
    font-weight: bold;
    margin-right: .4em;
    cursor: pointer;
    font-size: 1em;
    color: green;
}

.obut:focus {
    outline: none;
}