body {
    background-color: wheat;
}

.nl {
    display: flex;
    list-style: none;
    text-decoration: none;
    background-color: darkcyan;
    margin: 0;
    padding: 0;
}

li {
    margin: 1em;
    color: whitesmoke;
    font-weight: bolder;
}

.gap {
    flex: 1;
}

a {
    text-decoration: none;
    color: whitesmoke;
}

.h1n {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.h1n1 {
    text-transform: uppercase;
    font-weight:bold;
    letter-spacing: 1em;
    color:orange; 
    font-size: 1.5em;
    animation-name: mymove;
    animation-duration: 2.5s;  
}

@media (max-width:900px) {
    .h1n1 {
        letter-spacing: .4em;
    }
}

@media (max-width:600px) {
    .h1n1 {
        font-size: 1em;
        letter-spacing:.2em;
    }
}

@media (max-width:400px) {
    .h1n1 {
        font-size: 1.1em;
        letter-spacing: 0.1em;
    }
}

@media (max-width:350px) {
    .h1n1 {
        letter-spacing:0em;
    }
}

@keyframes mymove {
    0% {opacity: 0;
    transform: translate(-100%,0);}

    50% {
        opacity: .5;
        transform: translate(80%,0);
        filter: blur(1px);
        color: orangered;
    }

    100% {
        opacity: 1;
        transform: translate(0,0);
        filter: blur(0);
    }
}